.BuySellComponentWrp{
  display: inline-block;
}
.BuySellTabsWrp{
  position: relative;
}
.BuySellForm{
  -webkit-box-shadow: -4px 20px 30px #ccc;
          box-shadow: -4px 20px 30px #ccc;
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
  border-radius: 8px;
  background-color: #fff;
  border: 1px solid transparent;
  padding: 15px 25px 20px;
}
.CurrenciesWrp{
  margin: 25px 0 10px;
}
.Specify{
  font-size: 14px;
  color: #41424e;
  margin-bottom: 30px;
}
.CoinWrp{
  display: inline-block;
  width: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.CoinWrp:nth-child(even){
  padding-left: 10px;
}
.CoinWrp:nth-child(odd){
  padding-right: 10px;
}
.Checked{
  height: 27px;
  width: 27px;
  border-radius: 100%;
  padding: 3px 8px 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: -webkit-linear-gradient(345deg, #d52882, #632572);
  background: -o-linear-gradient(345deg, #d52882, #632572);
  background: linear-gradient(105deg, #d52882, #632572);
  position: absolute;
  right: 0;
  bottom: 80%;
  display: none;
}
.Checked img{
  width: 100%;
}
.CurrentPriceWrp, .InputsWrp{
  overflow: hidden;
  margin: 35px 0 20px;
}
.InputsWrp{
  text-align: center;
}
.CurrentPriceWrp .Amount{
  float: left;
  font-weight: 700;
  font-size: 14px;
}
.CurrentPriceWrp .Price{
  float: right;
  font-size: 14px;
}
.TransactionFee{
  font-size: 12px;
  color: #555;
  text-align: right;
}
.BuyBtnWrp{
  text-align: center;
  margin: 40px 0 30px;
}



